.messages {
	overflow-y: scroll;
	overflow-x: hidden;
	height: 70vh;
}

.rollButton {
	width: 100%;
	border: 1px solid #000;
	background-color: rgb(52, 131, 45);
	padding: 10px 20px;
}

.chat {
	height: 80vh;
}

.chatHeader {
	font-size: 12px;
	text-align: center;
	margin-bottom: 20px;
	text-decoration: underline;
}

.username {
	font-weight: bold;
}

.chatInputWrapper {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: stretch;
}
